/** @jsxImportSource @emotion/react */
import React, { useState } from "react";
import { useNavigate } from "react-router";
import { css } from '@emotion/react';
import { useStore } from 'store/store';
import HomeText from "constants/HomeText";
import BaseButton from "components/BaseButton";
import image_scroll from "assets/images/home/image_scroll.png";

const FullScreen: React.FC = () => {

  const navigate = useNavigate();
  const { currentItemIndex, setCurrentItemIndex } = useStore();
  const currentSubText = HomeText[currentItemIndex]?.subText || [];

  const handleButtonClick = () => {
    setCurrentItemIndex((prevIndex) => (prevIndex + 1) % 3);
  };

  return (
    <div css={css`
              position: relative;
              display: flex;
              justify-content: space-between;
              align-items: center;
              width: 100%;
              height: 100%;
              padding: 0px 80px;
              background-color: ${HomeText[currentItemIndex]?.backgroundColor}
            `}
    >
      <div css={css`
                display: flex;
                flex-direction: column;
                justify-content: center;
                height: 100%;
                text-align: left;
              `}
      >
        <p css={css`
                align-self: flex-start;
                font-size: 32px;
                line-height: 40px;
                background: linear-gradient(to right, #82E5A8, #39945C );
                -webkit-background-clip: text;
                color: transparent;
              `}
        >
          HyperShare
        </p>
        <p css={css`
                align-self: flex-start;
                color: ${currentItemIndex === 1 ? "#fff":"#000"};
                font-size: 70px;
                font-weight: 700;
                line-height: 78px;
              `}
        >
          {HomeText[currentItemIndex]?.mainText1}
        </p>
        <p css={css`
                align-self: flex-start;
                font-size: 70px;
                font-weight: 700;
                line-height: 78px;
                background: linear-gradient(to right, #39945C, #82E5A8 );
                -webkit-background-clip: text;
                color: transparent;
                padding-bottom: 10px;
              `}
        >
          {HomeText[currentItemIndex]?.mainText2}
        </p>
        <div css={css`max-width: 65%;`}
        >
        {currentSubText.map((text, index) => (
          <p 
            key={index}
            css={css`
                  color: ${currentItemIndex === 1 ? "#9e9e9e":"#666"};
                  font-size: 14px;
                  line-height: 22px;
                  padding-bottom: 16px; 
                `}
          >
            {text}
          </p>
        ))}
        {currentItemIndex !== 0 && (
          <BaseButton
            text={HomeText[currentItemIndex]?.buttonText}
            style={css`
                    font-size: 20px;
                    font-weight: 700;
                    border: 0;
                    color: #fff;
                    background-color: #39945C;
                    padding: 16px 30px;
                    height: auto;
                  `}
            onClick={() => currentItemIndex === 1 ? navigate("/proposal") : navigate("/proposal/detail")}
          />
        )}
        </div>
      </div>
      <div css={css`text-align: right;`}>
        <img 
          alt="image_main" 
          src={HomeText[currentItemIndex]?.mainImage}
          css={css`max-width: 100%; height: auto; display: block;`}
        />
      </div>
      <div css={css`
                display: flex;
                flex-direction: column;
                align-items: center;
                gap: 40px;
                position: absolute;
                bottom: 40px;
                left: 50%;
                transform: translateX(-50%);
              `}
      >
        <img 
          alt="image_button" 
          src={HomeText[currentItemIndex]?.buttonImage}
          onClick={handleButtonClick}
        />
        <img 
          alt="scroll" 
          src={image_scroll}
        />
      </div>
    </div>
  );
};

export default FullScreen;
