/** @jsxImportSource @emotion/react */
import React, { useState, useRef, useEffect } from "react";
import { useNavigate } from "react-router-dom";
import { css } from "@emotion/react";
import Slider from "react-slick";
import useApi from "hooks/useApi";
import BaseButton from "components/BaseButton";
import BaseCard from "components/BaseCard";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import SwiperTest from "./SwiperTest";
import hyjelly from "assets/images/proposal/image_proposal_hyjelly.png";

interface Proposal {
  id: number;
  ownerAddress: string;
  proposalAddress: string;
  status: string;
  deadline: string;
  lockDate: string;
  interestRate: number;
  licenseQuantity: string;
  backingFund: number;
  targetAmount: number;
  raisedAmount: number;
  resultCode: string;
}

const ScrollSection: React.FC = () => {
  const navigate = useNavigate();
  const sliderRef = useRef<Slider>(null);
  const [isHovered, setIsHovered] = useState(false);
  const [slidesToShow, setSlidesToShow] = useState(3);
  const endpoint = "v1/proposal?page=0&size=10&sort=NORMAL";
  const { data, loading, error, execute } = useApi({ endpoint, method: "GET" });

  useEffect(() => {
    execute();
    setSlidesToShow(getInitialSlidesToShow());
  }, []);

  useEffect(() => {
    window.addEventListener("resize", handleResize);
    return () => {
      window.removeEventListener("resize", handleResize);
    };
  }, []);

  const settings = {
    dots: false,
    infinite: true,
    speed: 4000,
    slidesToShow: slidesToShow,
    slidesToScroll: 1,
    autoplay: true,
    autoplaySpeed: 0,
    cssEase: "linear",
    pauseOnHover: false,
  };

  const getInitialSlidesToShow = () => {
    const screenWidth = window.innerWidth;
    if (screenWidth > 1650) {
      return 5;
    } else if (screenWidth > 1400) {
      return 4;
    } else if (screenWidth > 1200) {
      return 3;
    } else if (screenWidth > 750) {
      return 2;
    } else {
      return 1;
    }
  };

  const handleResize = () => {
    setSlidesToShow(getInitialSlidesToShow());
  };

  const handleMouseEnter = () => {
    setIsHovered(true);
    sliderRef.current?.slickPause();
  };

  const handleMouseLeave = () => {
    setIsHovered(false);
    sliderRef.current?.slickPlay();
  };

  if(loading) {
    return null;
  }

  if(error) {
    return <p>error: {error}</p>;
  }

  return (
    <div css={css`padding: 50px 0px`}>
      {/* proposal */}
      <h2 css={css`
                font-size: 50px;
                background: linear-gradient(to right, #6EE987, #39945C );
                -webkit-background-clip: text;
                color: transparent;
                padding: 100px 80px 80px;
              `}
      >
        Token Pool Proposal
      </h2>
      <div>
        <div
          onMouseEnter={handleMouseEnter}
          onMouseLeave={handleMouseLeave}
        >
          <Slider 
            ref={sliderRef}
            {...settings}
          >
            
          </Slider>
        </div>
        <div css={css`padding: 40px 80px 100px;`}>
          <BaseButton
            text="READ ALL"
            style={css`
                    height: auto;
                    color: #fff;
                    background-color: #000;
                    padding: 8px 20px;
                  `}
            onClick={() => navigate("/proposal")}
          />
        </div>
      </div>

      {/* help */}
      <div css={css`
                display: flex; 
                justify-content: space-between;
                padding: 0px 80px;
              `}
      >
        <div>
          <h2 css={css`
                    font-size: 50px;
                    background: linear-gradient(to right, #6EE987, #39945C );
                    -webkit-background-clip: text;
                    color: transparent;
                    padding: 100px 0px 80px;
                  `}
          >
            Help
          </h2>
          <p css={css`
                  font-size: 32px; 
                  line-height: 40px;
                `}
          >
            Securing the
          </p>
          <p css={css`
                  font-size: 32px; 
                  line-height: 40px;
                  color: #39945c;
                `}
          >
            Internet of AI
          </p>
        </div>
        <div>
          <img alt="metamong" src={hyjelly} css={css`max-width: 100%;`} />
        </div>

      </div>

    </div>
  );
};

export default ScrollSection;
